















































.provider-card {
  width: 16.5rem;
  border: 2px solid rgb(216, 216, 216);
  &:hover{
    box-shadow: 10px 10px 2px -5px #E7E7E7;
  }
}

.provider-name {
  font-weight: 800;
}

.provider-logo {
  height: 12rem;
  white-space: nowrap;
  position: relative;

  a {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  img {
    max-height: 10rem;
  }
}
