















































.scroll {
  right: 10px;
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 10px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.25);
  background-color: #5fd1fc;
  color: #fff;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  transition: all 5s ease-in;
  z-index:5;
}

.scroll:hover {
  background-color: #1e96c2;
}

