




































































@import "node_modules/bulma/sass/utilities/initial-variables";
@import "node_modules/bulma/sass/utilities/derived-variables";
@import "node_modules/bulma/sass/utilities/mixins";
@import "node_modules/@creativecommons/vocabulary/scss/spacing";

$hero-height: 74vh;

.hero {
  background: #fff;
  position: relative;
  height: $hero-height;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 300px;

  .hero_search-form {
    position: relative;
    max-width: 750px;
    width: 100%;
    padding: 0 0.5em 0 0.5em;
  }

  .centered-search-box {
    justify-content: center;

    @media screen and (max-width: $tablet - 1px) {
      .input-field {
        width: 100%;
      }
      .button {
        font-size: 1rem;
        padding: $space-small calc(#{$space-normal} + .2rem);
      }
    }

    @media screen and (min-width: $tablet) {
      .input-field {
        input {
          width: 570px;
          font-size: 1.75rem;
          height: 5.063rem;
          padding-left: $space-big;
        }
      }
      .button {
        font-size: 1.75rem;
        padding: calc(#{$space-large} - .093rem) $space-large;
      }
    }
  }


  /* Small only */
  @include mobile {
    height: 80vh;
  }
}

.help-links {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
}

.help-icon {
  height: 32px;
  vertical-align: middle;
}

.locale-changer {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
}
