.main-footer {
    background-color: $color-black;
    color: $white;
    padding: $space-large 0;
    a {
        color: $color-dark-turquoise;
        &:hover {
            text-decoration: underline;
        }
    }
    .main-logo {
        display: block;
        svg {
            width: 13rem;
        }
    }
    address {
        font-style: normal;
    }
    .footer-navigation {
        .menu {
            li {
                display: inline-block;
                &:first-child {
                    .menu-item {
                        padding-left: 0;
                    }
                }
                .menu-item {
                    display: block;
                    padding: $space-normal;
                    font-family: $family-alternative;
                    font-size: $font-size-h6;
                    font-weight: 600;
                    text-transform: uppercase;
                }
            }
        }
    }
    .subscription {
        h5 {
            margin-bottom: $space-normal;
            color: $color-white;
        }
        .newsletter {
            display: flex;
            width: 70%;
            .input {
                background-color: transparent;
                border-color: $color-dark-gray;
            }
            .button {
                margin-left: -1rem;
                border: none;
                background-color: $color-dark-gray;
                color: $color-white;
                &:hover {
                    background-color: lighten($color-dark-gray,10);
                }
            }
        }
    }
    .donate-section {
        h5 {
            color: $color-white;
        }
        .donate {
            margin-top: $space-big;
            color: $color-black;
            svg {
                margin-right: 1rem;
                width: 2rem;
                height: 2rem;
            }
        }
    }
    @include tablet() {
        padding: $space-big;
        .footer-navigation {
            .menu {
                display: flex;
            }
        }
    }
    @include mobile() {
        padding: $space-normal;
        .footer-navigation {
            margin-bottom: $space-normal;
            .menu {
                li {
                    display: block;
                    .menu-item {
                        padding-left: 0;
                    }
                }
            }
        }
    }
}