




















@import "bulma/sass/utilities/_all.sass";

// from http://www.cssarrowplease.com/
.license-explanation-tooltip {
  position: absolute;
  background: #ffffff;
  border: 2px solid #D8D8D8;
  z-index: 10;
  width: 20rem;
  box-shadow: 10px 10px 10px -10px rgba(0,0,0,0.25);
  @include desktop {
    margin-left: 20.5rem;
    margin-top: -2.6rem;
  }
  @include touch {
    margin-top: .5rem;
  }
}
.license-explanation-tooltip:after, .license-explanation-tooltip:before {
  @include desktop {
    right: 100%;
    top: 7%;
  }
  @include touch {
    bottom: 100%;
    left: 10%;
  }
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.license-explanation-tooltip:after {
  border-color: rgba(255, 255, 255, 0);
  border-width: 10px;

  @include desktop {
    border-right-color: #ffffff;
    margin-top: 3px;
  }
  @include touch {
    border-bottom-color: #ffffff;
    margin-left: 258px;
  }
}
.license-explanation-tooltip:before {
  border-color: rgba(120, 120, 120, 0);
  border-width: 13px;

  @include desktop {
    border-right-color: #D8D8D8;
    margin-top: 0px;
  }
  @include touch {
    border-bottom-color: #D8D8D8;
    margin-left: 255px;
  }
}
