




















































































































































@import "node_modules/bulma/sass/utilities/_all";

.button[disabled] {
  opacity: 1;
}

.search-grid_analytics h5,
.search-grid_layout-control h5 {
  padding-top: 1.36vh;
  font-size: 1rem;
  display: inline-block;
}

.search-grid_layout-control h5 {
  margin-right: 10px;
}

.search-grid_layout-control {
  text-align: right;

  fieldset {
    display: inline;
    margin-right: 5px;
  }
}

.infinite-loading-container {
  margin-top: 30px;
  width: 100%;
}

.search-grid_ctr {
  .masonry-layout {
    margin: auto;
    margin-top: 1rem;
  }

  .item {
    width: 320px;
    margin-bottom: 20px;
  }
}

.search-grid:after {
  content: '';
  display: block;
  clear: both;
}

.search-grid_notification {
  width: 50%;
  margin: auto;
  font-weight: 500;
  text-align: center;
}

.search-grid-cells {
  display: flex;
  flex-wrap: wrap;
  margin: 10px;

  @include tablet {
    &:after {
      content: '';
      flex-grow: 999999999;
    }
  }
}

label {
  color: #333333;
}

.load-more {
  text-align: center;

  button {
    font-size: 1.2em;

    @include mobile {
      padding: .5rem;

      span{
        font-size: 0.9rem;
      }
    }
  }
}
.count{
  padding-top: 0.6rem;
  padding-left: 1.3rem;
}
