


















































































































@import "bulma/sass/utilities/_all.sass";

.toggle-filter {
  height: 3.875rem;
  text-transform: none;
  font-size: 13px;
  border: 2px solid #D8D8D8;
  box-sizing: border-box;
  border-radius: 4px;
  width: 68px;

  &.small {
    height: 2.5rem;
  }
}

.search-form {
  width: 100%;
  top: 0;
  position: sticky;
  background-color: #f5f5f5;
  z-index: 10;
}

.search-input {
  width: 70%;

  @include touch {
    width: 100%;
  }

  .control:first-child {
    width: 100%;
  }
}
.button .icon {
  height: auto;
}

.icon .search {
  padding: 1.3rem;

  @include touch {
    padding: .8rem;
  }
}
