


































@import "node_modules/bulma/sass/utilities/_all";

.home-page_body {

  /* Small only */
  @include mobile {
    margin: 60px 0 30px;
  }
}

.block {
  position: relative;
  height: 100%;
  width: 100%;
  max-height: 250px;
  overflow: hidden;
  border: 1px solid white;
  background: black;
  opacity: 0.8;
  transition: opacity 0.2s ease-out;
  cursor: pointer;

  &:hover {
    transition: opacity 0.2s ease-in;
    opacity: 1;

  }
}

.b1 {
  grid-area: big-top;

}

.b2 {
  grid-area :small-top;
}

.b3 {
  grid-area: small-middle;
}

.b4 {
  grid-area: big-middle;
}

.footer {
  position:fixed;

  /* Small only */
  @include mobile {
    position: relative;
  }
}
