





































































































































.search-grid_image-ctr {
  background: #EBECE4;
  display: block;
  width: 100%;
}

.search-grid_item-container {
  margin: 10px;
}

.search-grid_item {
  position: relative;
  width: 100%;
  overflow: hidden;

  i {
    display: block;
  }

  a {
    position: absolute;
    vertical-align: bottom;
    img {
      width: 100%;
    }
  }

  &:hover .overlay {
    opacity: 1;
    bottom: 0;
  }
}

.overlay {
  position: absolute;
  opacity: 0;
  transition: all .4s ease;
  color: #fff;
  display: block;
  top: -100%;

  &__top {
    top: 0;
    width: 100%;
    height: 2rem;
  }

  &__bottom {
    background-color: #000;
    bottom: -100%;
    top: auto;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 100%;
  }

  // Show on touch devices
  @media (hover: none) {
    position: absolute;
    opacity: 1;
    bottom: 0;
  }
}

.search-grid_item {
  width: 100%;
  position: relative;
  display: block;
  float: left;
  flex: 0 0 auto;
  flex-grow: 1;
  cursor: pointer;
}

.search-grid_image {
  margin: auto;
  display: block;
}

.search-grid_image__fill {
  width: 100%;
}
